"use client"
import { useState } from "react"
import React from "react"
import './signup.css'
import Link from "next/link"
import { Search } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
const Signup=()=> {
    
    const [inputMessage,setInputMessage]=useState({
        email:"",
        name:"",
        password:"",
        address:"",
        
    });
    const [isDropdownVisible, setIsDropdownVisible] = useState(false);
    const[isDisabled,SetDisabled]=useState(true);
    
   
 
    const HandleSignup =(e:any)=>{
        e.preventDefault()
        ValidSignup()

    }
    const ValidSignup = ()=>{
         if(inputMessage.email.length<5 ||inputMessage.name.length<5||inputMessage.password.length<5 ||inputMessage.address.length<5){
            SetDisabled(true)
        }
        else{
            SetDisabled(false)
        }
        
    }
    return (
      <>
         <div className="flex min-h-screen flex-col">
            <header className="sticky top-0 z-50 w-full border-b bg-background/95:bg-background/60">
            <div className="container flex h-16 items-center">
            <Link href="/" className="flex items-center gap-2">
            <span className="text-2xl font-bold bg-gradient-to-r from-purple-600 to-pink-600 bg-clip-text text-transparent">
              Resonance
            </span>
            </Link>
          <div className="ml-auto flex items-center gap-4">
            <form className="relative w-full max-w-sm lg:max-w-lg">
              <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input type="search" placeholder="Search songs, artists or genres..." className="pl-8 w-full" />
            </form>
            <Button>Sign In</Button>
          </div>
        </div>
      </header>
            
            <form
                onSubmit={HandleSignup}>
        
            
            <input
            type="email"
            value={inputMessage.email} 
            required={true}
            placeholder="enter your email"
            onChange={(e)=> {ValidSignup();
                setInputMessage((prev=>({...prev,email:e.target.value})))}}/>
            
           
            
             <input
             type="text"
             value={inputMessage.name} 
             required={true}
             placeholder="enter your name"
             onChange={(e)=> {ValidSignup();
                setInputMessage((prev=>({...prev,name:e.target.value})))}}
             />
            <input
             type="password"
             value={inputMessage.password} 
             required={true}
             placeholder="enter your password"
             onChange={(e)=> {ValidSignup();
                setInputMessage((prev=>({...prev,password:e.target.value})))}}
            />
            <input type="address"
             value={inputMessage.address} 
             required={true}
             placeholder="enter your address"
             onChange={(e)=> { setIsDropdownVisible(e.target.value.length > 0);
                ValidSignup();setInputMessage((prev=>({...prev,address:e.target.value})))}}
             onBlur={() => setTimeout(() => setIsDropdownVisible(false), 100)} // Optional: hide dropdown on blur
             onFocus={() => inputMessage.address.length > 0 && setIsDropdownVisible(true)} // Show on focus if there is input
        />
            {isDropdownVisible && (
                <ul>
                    <li>
                        {"something"}
                    </li>
                </ul>
        )}
        <input type="submit" disabled={isDisabled} value="sign up"/>
            
            
            <button> <a href="login">Click here if you already have a account</a></button>
        </form>
    </div>  
      </>
    )
  }
  
  export default Signup